.grid {
  width: 100%;
  display: block;
  padding: 0;
}
.grid.wide {
  max-width: 1200px;
  margin: 0 auto;
}
.row {
  display: flex;
  flex-wrap: wrap;
  /* margin để kéo lại, cho column ở dưới thẳng cột */
  margin-left: -4px;
  margin-right: -4px;
}
.row.no-gaps {
  margin-left: 0;
  margin-right: 0;
}

.col {
  padding-left: 4px;
  padding-right: 4px;
}
.row.no-gaps .col {
  padding-left: 0;
  padding-right: 0;
}

/* c-0 là dùng để ẩn column trên mobile */
.c-0 {
  display: none;
}

/* từ c-1 đến c-12 là kích thước column */
/* kích thước của c-12 = (12 * c-1), giống như thẻ h1, h2, ... */
.c-1 {
  flex: 0 0 8.333333333333333%;
  max-width: 8.333333333333333%;
}
.c-2 {
  flex: 0 0 16.66666666666667%;
  max-width: 16.66666666666667%;
}
.c-3 {
  flex: 0 0 25%;
  max-width: 25%;
}
.c-4 {
  flex: 0 0 33.33333333333333%;
  max-width: 33.33333333333333%;
}
.c-5 {
  flex: 0 0 41.66666666666667%;
  max-width: 41.66666666666667%;
}
.c-6 {
  flex: 0 0 50%;
  max-width: 50%;
}
.c-7 {
  flex: 0 0 58.33333333333333%;
  max-width: 58.33333333333333%;
}
.c-8 {
  flex: 0 0 66.66666666666667%;
  max-width: 66.66666666666667%;
}
.c-9 {
  flex: 0 0 75%;
  max-width: 75%;
}
.c-10 {
  flex: 0 0 83.33333333333333%;
  max-width: 83.33333333333333%;
}
.c-11 {
  flex: 0 0 91.66666666666667%;
  max-width: 91.66666666666667%;
}
.c-12 {
  flex: 0 0 100%;
  max-width: 100%;
}

/* Căn chỉnh Column nằm ở giữa Row*/
/* VD: 
    flex-item chiếm 6 cột, còn trống 6 cột.
    Thì ta chia đều cột còn trống, mỗi bên 3 cột.
    -> flex-item sẽ nằm giữa Row
    3 cột, tương ứng lấy c-o-3 (cùng số 3)
 */
.c-o-1 {
  margin-left: 8.333333333333333%;
}
.c-o-2 {
  margin-left: 16.66666666666667%;
}
.c-o-3 {
  margin-left: 25%;
}
.c-o-4 {
  margin-left: 33.33333333333333%;
}
.c-o-5 {
  margin-left: 41.66666666666667%;
}
.c-o-6 {
  margin-left: 50%;
}
.c-o-7 {
  margin-left: 58.33333333333333%;
}
.c-o-8 {
  margin-left: 66.66666666666667%;
}
.c-o-9 {
  margin-left: 75%;
}
.c-o-10 {
  margin-left: 83.33333333333333%;
}
.c-o-11 {
  margin-left: 91.66666666666667%;
}

